*{margin: 0;
padding: 0;
box-sizing: border-box;
}
.displaycontainer{
    width: 51%;
    height: 95%;
    overflow: hidden;
    border-radius: 10px;
    background-color: white;
    flex-shrink: 0;
    margin: 0px 15px;
}
.pic{
    width: 100%;
   height: 60%;
}
.pic img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.imagetitle{
    margin: 0px 15px;
    height: 30%;
}
.imagetitle>h3{margin-top: 5px;}
.paragraph{
    height: 40%;
    margin-top: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;

}
.post{
    height: 10%;
    font-size: 14px;
    text-align: center;
    line-height: 4.5vh;
    border-top: 1px black solid;
    font-weight: 700;
}
.titleflex{
    display: flex;
    justify-content: space-between;
    margin-top: 1.1vh;
}
.displaytitlename{font-size: 1.8vh;}
.displaytitlename>span:first-child{
    border-radius: 50%;
    padding:0.5vh;
    background-color: pink;
    margin-right: 0.5vw;
}
.displaytitlename>span:last-child{
    border-radius: 50%;
    padding:0.5vh;
    background-color:rgb(161, 219, 241);
    margin-left: 0.5vw;
}
.displaystars{
    font-size:2vh;
    color: rgb(51, 99, 201);
}




