.container{
    width: 45%;
    margin: 20px auto;
    position: relative;
}

.containerimage{
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.85);
    display: none;
    animation: animate 0.3s ease-in-out;
}
.cancel{
    text-align: right;
    margin: 1.5vh 1.5vh;
}
.cancel span{
    padding: 8px 8px;
    border-radius: 50%;
    background-color: white;
    cursor: pointer;
}
.containsimages{
    display: flex;
    height: 82%;
    margin-top: 5vh;
    overflow: auto;
}
.containsimages::-webkit-scrollbar{
    height: 7px;
}
.containsimages::-webkit-scrollbar-track{
background-color: rgba(255, 255, 255, 0.05);
}
.containsimages::-webkit-scrollbar-thumb{
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 3.5px;
}
.containsimages::-webkit-scrollbar-thumb:hover{
    background-color: rgba(255, 255, 255, 0.8);
}

@keyframes animate {
    from{transform: scale(0,0);}
    to{ transform: scale(1,1);}
}
@media screen and (max-width:1024px) {
    .container{
        margin: 0px;
        width: 100%;
    }
    .imagetitle{
        font-size: 1.4vw;
    }
    .displaystars{
        font-size:2.5vw;
    }
    .displaytitlename{
        font-size: 1.8vw;
    }
    .paragraph{
        margin-top:3px;
    }
    .post{line-height: 3.5vw;}
    
}
@media screen and (max-width:500px) {
    .dates{
        height: 72vh;
    }
    .weekly>div{
        height: 20vh;
    }
    .stars{
        font-size: 2vw;
    }
    .titlename{
        font-size: 2.5vw;
    }
    
    .footbar>div{
        padding-top: 1vh;
    }
    .containsimages{

       height: 65%;
        margin-top: 10vh ;
    }
    .paragraph{
        margin: 0px;
    }
    .imagetitle{
        font-size:2.2vh;
    }
    .imagetitle>h3{
        margin-top: 10px;
    }
    .post{line-height: 4.5vh;}

}


