.dates{
    margin-top: 10px;
    height: 75vh;
    overflow: auto;
    font-size: 2vh;
}

.dates::-webkit-scrollbar{
    width: 7px;
}
.dates::-webkit-scrollbar-track{
background-color: rgba(0, 0, 0, 0.1);
}
.dates::-webkit-scrollbar-thumb{
    background-color: rgba(0, 0, 0, 0.3);
    border-radius: 3.5px;
}
.dates::-webkit-scrollbar-thumb:hover{
    background-color: rgba(0, 0, 0, 0.7);
}
.weekly{
    display: flex;
    text-align: center;
}
.weekly>div{
    flex: 1;
    overflow: hidden;
    height: 20vh;
    border-right: 0.3px rgb(214, 209, 209) solid;
    border-bottom: 0.3px rgb(214, 209, 209) solid;
}
.weekly>div:last-child{border-right: none;}
.weekly>div:first-child{
    background-color: rgb(214, 209, 209);
    border-top: 0.3px rgb(214, 209, 209) solid ;
}
.stars{
    font-size: 1.2vh;
    color: rgb(51, 99, 201);
}
.thumbnailcontainer{
    height: 85%;
}
.thumbnail{
    height: 60%;
}
.thumbnail img{
    width :80%;
    height:100%;
    object-fit: cover;

}
.titlename{
    font-size: 1.5vh;
    margin-top: 0.8vh;
    height: 10%;
}
.titlename>span:first-child{
    border-radius: 50%;
    padding:0.5vh;
    background-color: pink;
    margin-right: 0.5vw;
}
.titlename>span:last-child{
    border-radius: 50%;
    padding:0.5vh;
    background-color:rgb(161, 219, 241);
    margin-left: 0.5vw;
}

